html{
    font-size: 16px;
}
@mixin center{
    justify-content: center;
    align-items: center;
}
.intro{
    max-height: 100vh;
    overflow: hidden;
    position: relative;
}
.intro:after{
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.42) 0.01%, rgba(156, 150, 150, 0.483791) 99.98%, rgba(171, 164, 164, 0.489583) 99.99%, rgba(255, 255, 255, 0) 100%);
    width: 100%;
    height: 100%;
}
.video{
    position: relative;
    padding-bottom: 56.25%;
    max-height: 100vh;
    overflow: hidden;
    z-index: 1;
}
.video__media{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
}
.contactUs{
    position: absolute;
    top: 0;
    z-index: 3;
    left: 0;
    right: 0;
    margin-right: 5%;
}
.social{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 2rem;
}
.social p{
    font-family: Oswald;
    font-size: 1.56rem;
    color: #BABBBD;
    letter-spacing: 0.215em;
    line-height: 44px;
    font-weight: 300;
    font-style: normal;
    margin-top: 0;
    margin-bottom: 0rem;
}
.socialNetworks{
    display: flex;
    width: 8rem;
    justify-content: space-around;

}
.socialNetworks__icons img{
    width: 1.88rem;
    height: 1.88rem;
}
.portfolio{
    position: absolute;
    height: 100%;
    z-index: 3;
    left: 0;
    right: 0;
    top: 0;
    
}
.portfolio__title{
    display: flex;
    flex-direction: column;
    @include center();
    height: 100%;
}
.portfolio__title h1{
    font-family: "Oswald";
    font-size: 7.12rem;
    text-transform: uppercase;
    line-height: 11rem;
    font-weight: 400;
    letter-spacing: 0.305em;
    color: #F1F5FF;
}
.portfolio__title h3{
    font-family: 'Oswald';
    font-weight:300;
    color: #FFFFFF;
    font-size: 2.9rem;
    line-height: 70px;
    padding-left: 24rem;
    height: 5rem;
}
.upperShadow{
    height: 13.25rem;

}
.sites{
    display: flex;
    justify-content: space-between;
}
.sites__items{
    height: 297px;
    background-color: #c0c0c0;
    width: 528px;
    margin-bottom: 11rem;
    overflow: hidden;
    box-shadow: 0px 0px 8px 9px rgba(0, 0, 0, 0.26);
    
    
    
}
.sites__items img{
    height: 297px;
    width: 528px;
    transition: all 1s ease-out;
    
    
}
.sites__items img:hover{
    transform: scale(1.2);
   
    
}

@media (max-width:1200px){
    html{
        font-size: 12px;
    }
    .sites{
        flex-direction: column;
        align-items: center;
    }

}
@media (max-width:992px){
    html{
        font-size: 10px;
    }

}
@media (max-width:768px){
    html{
        font-size: 9px;
    }
    
    .sites__items{
        height: 219px;
        width: 390px;
    }
    .sites__items img{
        height: 219px;
        width: 390px;
    }
}
@media(max-width:576px){
    
    html{
        font-size: 5px;
    }
    .sites__items{
        height: 107px;
        width: 191px;
    }
    .sites__items img{
        height: 107px;
        width: 191px;
    }

}
@media(max-width:400px){
    html{
        font-size: 4px;
    }


}